import React, {useEffect, useState} from 'react';
import ReactMarkdown from 'react-markdown'
import {Col, Container, Row} from "react-bootstrap";
import rehypeRaw from 'rehype-raw'

function InfoSection(props: { mdFilePath: string, children?: JSX.Element }) {

    const [text, setText] = useState<string>();

    useEffect(() => {

        fetch(props.mdFilePath)
            .then((response) => response.text())
            .then((text) => {

                const technologies = ['asp.net core', 'docker compose', 'signalr', 'razor', 'react', 'typescript', 'pure javascript', 'jquery', 'ms sql server', 'redux', 'nodejs', 'asp.net mvc3-5', 'postgresql', 'mongodb', 'docker', 'google cloud', 'azure', 'rabbitmq', '.net compact framework', 'winforms'];

                let txt = text;

                const regEx = new RegExp(`(${technologies.join('|')})([\,\s\t]+)*`, "ig");
                const replaceMask = '<span class="tech">$1</span>';

                txt = txt.replace(regEx, replaceMask);
                //
                // technologies.forEach(x => txt = txt.replaceAll(`G/${x}.*/i`, '<div class="tech">$1</div>'));

                setText(txt);
            });

    }, [props.mdFilePath]);

    return (

        <Row>
            <Col>
                <Row>
                    <Col>
                        {props.children}
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <ReactMarkdown
                            skipHtml={false}
                            rehypePlugins={[rehypeRaw]}
                            children={text as string}/>
                    </Col>
                </Row>
            </Col>
        </Row>
    );
}

export default InfoSection;